.sitg-modal-content {
  position: absolute;
  top: 68px;
  left: 0;
  right: 0;
  width: 100%;
  bottom: 80px;
  background: #fff;
  padding: 32px 0;
  overflow: auto;
  font-size: 16px;
  line-height: 1.5;
}

.sitg-container {
  position: relative;
  padding-left: 32px;
  padding-right: 32px;
}

.sitg-modal-row {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.sitg-modal-col {
  position: relative;
  width: 100%;
}

.sitg-modal-col {
  flex: 0 0 50%;
  max-width: 50%;
}

@media (max-width: 767.98px) {
  .sitg-modal-col {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.dm-uploader,
.sitg-btn-row {
  margin-right: 16px;
}

.sitg-modal-filelist {
  margin-left: 16px;
}

.sitg-modal-filelist {
  padding: 0 16px;
  border: 1px solid #ccd0d4;
}

.sitg-modal-filelist-title {
  font-size: 16px;
  margin: 0 -16px;
  padding: 10px 16px;
  background: #eee;
}

.sitg-modal-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 30px;
  border-top: 1px solid #ccc;
  background: #eee;
}

.sitg-btn-row {
  text-align: right;
  margin-bottom: 2rem;
}

.wp-core-ui .sitg-upload-start,
.wp-core-ui .sitg-upload-cancel {
  font-size: 16px;
}

.sitg-upload-start i,
.sitg-upload-cancel i {
  line-height: 1.875;
}

.wp-core-ui .sitg-upload-cancel {
  display: none;
  margin-right: 1rem;
}

.sitg-modal-delete-file {
  display: inline-block;
}

.sitg-modal-delete-file span {
  display: block;
  margin-left: 1rem;
  margin-top: 3rem;
  width: 26px;
  height: 26px;
  text-align: center;
  line-height: 26px;
  color: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  cursor: pointer;
}

.sitg-modal-delete-file:hover span {
  color: red;
}

.sitg-status-bar {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-top: 0;
}

.sitg-status-content {
  display: block;
  margin-bottom: 0.5rem;
}

.dm-uploader {
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.dm-uploader .btn {
  position: relative;
  overflow: hidden;
}

.dm-uploader .btn input[type="file"] {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  border: solid transparent;
  width: 100%;
  opacity: .0;
  filter: alpha(opacity=0);
  cursor: pointer;
}

#debug {
  overflow-y: scroll !important;
  height: 180px;
}

.dm-uploader {
  border: 0.25rem dashed #A5A5C7;
  text-align: center;
}

.dm-uploader.active {
  border-color: red;
  border-style: solid;
}

.sitg-modal-filelist .preview-img {
  display: inline-block;
  width: 80px;
  height: 80px;
  background-color: rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.sitg-modal-filelist .preview-img img {
  display: inline-block;
  width: 100%;
  height: auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.sitg-confirm-modal {
  position: fixed;
  top: 30px;
  left: 30px;
  right: 30px;
  bottom: 30px;
  z-index: 160000;
}

.sitg-confirm-content {
  width: 100%;
  max-width: 300px;
  margin: auto;
  padding: 2rem;
  background: #fcfcfc;
  -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.7);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.7);
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.sitg-confirm-title {
  margin-top: 0;
  margin-bottom: 1.5rem;
}

.wp-core-ui .sitg-confirm-btn {
  margin: 0 0.5rem 0 0;
}
